/* ndrf.css */
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .container-ndrf {
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
  }
  
  .image-passage-container {
    display: flex;
    align-items: flex-start;
    margin-top: 50px;
  }
  
  .image {
    margin: 20px;
  }
  
  .passage {
    max-width: 600px;
  }
  
  .passage p {
    margin: 0;
    padding: 5px 0;
  }
  
  img {
    max-width: 100%;
    height: auto;
  }
  