/* Weather.css */

.weather {
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height:20px;
  margin: 0.5%;
}

.weather-data {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.temperature {
  display: flex;
  align-items: center;
}

#icon img {
  width: 100px;
  height: 100px;
}

.box {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  margin-left: 10px;
}

.temp-c {
  text-align: center;
}

.description {
  text-transform: capitalize;
  font-size: 0.8rem;
  margin-top: 5px;
}
